// src/App.js
import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import LoginPage from './pages/LoginPage';
import LecturerReportForm from './pages/LecturerReportForm';
import PRLReports from './pages/PRLReports';
import PLDashboard from './pages/PLDashboard';
import StudentDashboard from './pages/StudentDashboard';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

function App() {
  const [user, setUser] = useState(null); // { role: 'lecturer' }

  const handleLogin = (role) => {
    setUser({ role });
  };

  const handleLogout = () => {
    setUser(null);
    localStorage.removeItem('userRole');
  };

  return (
    <Router>
      <div className="App">
        {/* Navbar */}
        <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
          <div className="container">
            <span className="navbar-brand">LUCT Reporting System</span>
            {user && (
              <div className="d-flex">
                <span className="text-light me-3">Role: <strong>{user.role.toUpperCase()}</strong></span>
                <button className="btn btn-light btn-sm" onClick={handleLogout}>Logout</button>
              </div>
            )}
          </div>
        </nav>

        <div className="container mt-4">
          {!user ? (
            <LoginPage onLogin={handleLogin} />
          ) : (
            <Routes>
              <Route path="/" element={<Navigate to={`/${user.role}/dashboard`} />} />
              <Route path="/lecturer/report" element={<LecturerReportForm />} />
              <Route path="/prl/reports" element={<PRLReports />} />
              <Route path="/pl/dashboard" element={<PLDashboard />} />
              <Route path="/student/dashboard" element={<StudentDashboard />} />
              <Route path="*" element={<Navigate to="/" />} />
            </Routes>
          )}
        </div>
      </div>
    </Router>
  );
}

export default App;