// src/pages/LecturerReportForm.js
import React, { useState } from 'react';
import { Container, Form, Button, Alert, Card } from 'react-bootstrap';
import { submitReport } from '../services/api'; // Uses axios POST
import { courses } from '../utils/courses'; // For auto-fill

function LecturerReportForm() {
  const [formData, setFormData] = useState({
    facultyName: '',
    className: '',
    weekOfReporting: '',
    dateOfLecture: '',
    courseName: '',
    courseCode: '',
    lecturerName: '',
    studentsPresent: '',
    totalRegisteredStudents: '', // Will be auto-filled
    venue: '',
    scheduledTime: '',
    topicTaught: '',
    learningOutcomes: '',
    recommendations: ''
  });

  const [success, setSuccess] = useState(false);
  const [error, setError] = useState('');

  // Handle input changes
  const handleChange = (e) => {
    const { name, value } = e.target;

    if (name === 'courseCode') {
      const selectedCourse = courses[value];
      if (selectedCourse) {
        setFormData((prev) => ({
          ...prev,
          courseCode: value,
          courseName: selectedCourse.name,
          totalRegisteredStudents: selectedCourse.totalStudents
        }));
      } else {
        setFormData((prev) => ({
          ...prev,
          courseCode: value,
          courseName: '',
          totalRegisteredStudents: ''
        }));
      }
    } else {
      setFormData({ ...formData, [name]: value });
    }
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      await submitReport(formData);
      setSuccess(true);
      setError('');
      
      // Reset form
      setFormData({
        facultyName: '',
        className: '',
        weekOfReporting: '',
        dateOfLecture: '',
        courseName: '',
        courseCode: '',
        lecturerName: '',
        studentsPresent: '',
        totalRegisteredStudents: '',
        venue: '',
        scheduledTime: '',
        topicTaught: '',
        learningOutcomes: '',
        recommendations: ''
      });

      // Hide success message after 5 seconds
      setTimeout(() => setSuccess(false), 5000);
    } catch (err) {
      setError(
        err.response?.data?.error || 
        'Failed to submit report. Make sure the backend is running.'
      );
      setSuccess(false);
    }
  };

  return (
    <Container className="mt-5">
      <Card>
        <Card.Header className="bg-primary text-white">
          <h4>Lecturer Reporting Form</h4>
        </Card.Header>
        <Card.Body>
          {success && (
            <Alert variant="success"> Report submitted successfully!</Alert>
          )}
          {error && <Alert variant="danger">{error}</Alert>}

          <Form onSubmit={handleSubmit}>
            {/* Row 1 */}
            <div className="row mb-3">
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Faculty Name</Form.Label>
                  <Form.Control
                    type="text"
                    name="facultyName"
                    value={formData.facultyName}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>
              </div>
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Class Name</Form.Label>
                  <Form.Control
                    type="text"
                    name="className"
                    value={formData.className}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>
              </div>
            </div>

            {/* Row 2 */}
            <div className="row mb-3">
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Week of Reporting</Form.Label>
                  <Form.Control
                    type="number"
                    name="weekOfReporting"
                    value={formData.weekOfReporting}
                    onChange={handleChange}
                    required
                    min="1"
                    max="15"
                  />
                </Form.Group>
              </div>
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Date of Lecture</Form.Label>
                  <Form.Control
                    type="date"
                    name="dateOfLecture"
                    value={formData.dateOfLecture}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>
              </div>
            </div>

            {/* Row 3 */}
            <div className="row mb-3">
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Course Name</Form.Label>
                  <Form.Control
                    type="text"
                    name="courseName"
                    value={formData.courseName}
                    onChange={handleChange}
                    readOnly
                    placeholder="Auto-filled from course code"
                  />
                </Form.Group>
              </div>
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Course Code</Form.Label>
                  <Form.Select
                    name="courseCode"
                    value={formData.courseCode}
                    onChange={handleChange}
                    required
                  >
                    <option value="">-- Select Course --</option>
                    {Object.keys(courses).map((code) => (
                      <option key={code} value={code}>
                        {code} - {courses[code].name}
                      </option>
                    ))}
                  </Form.Select>
                </Form.Group>
              </div>
            </div>

            {/* Row 4 */}
            <div className="row mb-3">
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Lecturer's Name</Form.Label>
                  <Form.Control
                    type="text"
                    name="lecturerName"
                    value={formData.lecturerName}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>
              </div>
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Venue of the Class</Form.Label>
                  <Form.Control
                    type="text"
                    name="venue"
                    value={formData.venue}
                    onChange={handleChange}
                  />
                </Form.Group>
              </div>
            </div>

            {/* Row 5 */}
            <div className="row mb-3">
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Scheduled Lecture Time</Form.Label>
                  <Form.Control
                    type="time"
                    name="scheduledTime"
                    value={formData.scheduledTime}
                    onChange={handleChange}
                  />
                </Form.Group>
              </div>
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Topic Taught</Form.Label>
                  <Form.Control
                    type="text"
                    name="topicTaught"
                    value={formData.topicTaught}
                    onChange={handleChange}
                  />
                </Form.Group>
              </div>
            </div>

            {/* Row 6 */}
            <div className="row mb-3">
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Actual Number of Students Present</Form.Label>
                  <Form.Control
                    type="number"
                    name="studentsPresent"
                    value={formData.studentsPresent}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>
              </div>
              <div className="col-md-6">
                <Form.Group>
                  <Form.Label>Total Registered Students</Form.Label>
                  <Form.Control
                    type="number"
                    name="totalRegisteredStudents"
                    value={formData.totalRegisteredStudents}
                    onChange={handleChange}
                    required
                    readOnly
                  />
                  <Form.Text muted>
                    This is auto-filled based on selected course.
                  </Form.Text>
                </Form.Group>
              </div>
            </div>

            {/* Learning Outcomes */}
            <Form.Group className="mb-3">
              <Form.Label>Learning Outcomes of the Topic</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                name="learningOutcomes"
                value={formData.learningOutcomes}
                onChange={handleChange}
              />
            </Form.Group>

            {/* Recommendations */}
            <Form.Group className="mb-3">
              <Form.Label>Lecturer's Recommendations</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                name="recommendations"
                value={formData.recommendations}
                onChange={handleChange}
              />
            </Form.Group>

            <Button variant="primary" type="submit" className="w-100 py-2">
               Submit Report
            </Button>
          </Form>
        </Card.Body>
      </Card>
    </Container>
  );
}

export default LecturerReportForm;