// src/services/api.js
import axios from 'axios';

const API_BASE_URL = 'http://localhost:5000/api';

export const getReports = () => axios.get(`${API_BASE_URL}/reports`);
export const submitReport = (reportData) => axios.post(`${API_BASE_URL}/reports`, reportData);
export const addPRLFeedback = (reportId, feedback) => 
  axios.put(`${API_BASE_URL}/reports/${reportId}/feedback`, { prlFeedback: feedback });
export const deleteReport = (id) => axios.delete(`${API_BASE_URL}/reports/${id}`);