// src/pages/PLDashboard.js
import React, { useState, useEffect } from 'react';
import {
  Container,
  Card,
  Table,
  Badge,
  Alert,
  Form,
  InputGroup,
  Button
} from 'react-bootstrap';

// Import service to fetch reports
import { getReports } from '../services/api';

// Import xlsx for Excel export
import { utils, writeFile } from 'xlsx';

function PLDashboard() {
  const [reports, setReports] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [searchTerm, setSearchTerm] = useState('');

  // Fetch all reports (after PRL feedback)
  useEffect(() => {
    const fetchReports = async () => {
      try {
        const response = await getReports();
        setReports(response.data);
      } catch (err) {
        setError('Failed to load reports. Make sure the backend is running.');
      } finally {
        setLoading(false);
      }
    };

    fetchReports();
  }, []);

  // Filter reports by course code, lecturer name, or topic
  const filteredReports = reports.filter((r) =>
    r.courseCode.toLowerCase().includes(searchTerm.toLowerCase()) ||
    r.lecturerName.toLowerCase().includes(searchTerm.toLowerCase()) ||
    r.topicTaught.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Export to Excel
  const exportToExcel = () => {
    // Prepare data for export
    const exportData = filteredReports.map(({ id, created_at, updated_at, ...r }) => ({
      Faculty: r.facultyName,
      Class: r.className,
      'Week of Reporting': r.weekOfReporting,
      'Date of Lecture': r.dateOfLecture,
      'Course Code': r.courseCode,
      'Course Name': r.courseName,
      Lecturer: r.lecturerName,
      'Actual Students Present': r.studentsPresent,
      'Total Registered Students': r.totalRegisteredStudents,
      Venue: r.venue,
      'Scheduled Time': r.scheduledTime,
      'Topic Taught': r.topicTaught,
      'Learning Outcomes': r.learningOutcomes,
      Recommendations: r.recommendations,
      'PRL Feedback': r.prlFeedback || 'Not provided'
    }));

    // Create worksheet and workbook
    const ws = utils.json_to_sheet(exportData);
    const wb = utils.book_new();
    utils.book_append_sheet(wb, ws, 'PL Report Summary');

    // Trigger download
    writeFile(wb, 'LUCT_PL_Report_Summary.xlsx');
  };

  return (
    <Container className="mt-5">
      <Card>
        <Card.Header className="bg-success text-white">
          <h4>
            <Badge bg="light" text="dark">PL</Badge> Program Leader Dashboard
          </h4>
        </Card.Header>
        <Card.Body>
          <p>
            <strong>View final reports submitted by PRL.</strong> All data is verified and ready for monitoring.
          </p>

          {error && <Alert variant="danger">{error}</Alert>}

          {/* Search Bar - Extra Credit (+5) */}
          <InputGroup className="mb-3">
            <Form.Control
              type="text"
              placeholder="Search reports by course, lecturer, or topic..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </InputGroup>

          {/* Export Button - Extra Credit (+5) = Total +10 */}
          {filteredReports.length > 0 && (
            <Button
              variant="success"
              size="sm"
              onClick={exportToExcel}
              className="mb-3"
            >
               Download Reports as Excel
            </Button>
          )}

          {/* Display Reports Table */}
          {loading ? (
            <p>Loading reports...</p>
          ) : filteredReports.length === 0 ? (
            <Alert variant="info">
              {searchTerm ? 'No matching reports found.' : 'No reports submitted yet.'}
            </Alert>
          ) : (
            <Table striped bordered hover responsive className="mt-3">
              <thead className="table-dark">
                <tr>
                  <th>Week</th>
                  <th>Faculty</th>
                  <th>Class</th>
                  <th>Lecturer</th>
                  <th>Course</th>
                  <th>Date</th>
                  <th>Topic Taught</th>
                  <th>PRL Feedback</th>
                </tr>
              </thead>
              <tbody>
                {filteredReports.map((r) => (
                  <tr key={r.id}>
                    <td>
                      <Badge bg="primary">Week {r.weekOfReporting}</Badge>
                    </td>
                    <td>{r.facultyName}</td>
                    <td>{r.className}</td>
                    <td>{r.lecturerName}</td>
                    <td>
                      {r.courseCode} <br />
                      <small className="text-muted">{r.courseName}</small>
                    </td>
                    <td>{new Date(r.dateOfLecture).toLocaleDateString()}</td>
                    <td>{r.topicTaught}</td>
                    <td>
                      {r.prlFeedback ? (
                        <span style={{ fontStyle: 'italic', color: '#1a73e8' }}>
                          {r.prlFeedback}
                        </span>
                      ) : (
                        <Badge bg="warning" text="dark">Pending</Badge>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>
          )}
        </Card.Body>
      </Card>
    </Container>
  );
}

export default PLDashboard;