// src/pages/StudentDashboard.js
import React, { useState, useEffect } from 'react';
import {
  Container,
  Card,
  Table,
  Badge,
  Alert,
  Modal,
  Button,
  Form
} from 'react-bootstrap';
import { getReports } from '../services/api';

function StudentDashboard() {
  const [reports, setReports] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedReport, setSelectedReport] = useState(null);
  const [rating, setRating] = useState(0);
  const [showModal, setShowModal] = useState(false);

  // Simulate logged-in student
  const studentName = "Nthabeleng Mokoena";
  const program = "DIT Year 2 A";

  // Fetch reports from lecturers (monitoring)
  useEffect(() => {
    const fetchReports = async () => {
      try {
        const response = await getReports(); //  Already calls /api/reports
        // Filter only relevant class (mock monitoring)
        const filtered = response.data.filter(r => r.className === program);
        setReports(filtered);
      } catch (err) {
        setError('Failed to load lecture data. Please try again later.');
      } finally {
        setLoading(false);
      }
    };

    fetchReports();
  }, [program]);

  // Handle rating submission
  const handleRate = (report) => {
    setSelectedReport(report);
    setRating(0); // Reset
    setShowModal(true);
  };

  const submitRating = () => {
    alert(`Thank you, ${studentName}! You rated "${selectedReport.topicTaught}" with ${rating} star(s).`);
    setShowModal(false);
  };

  return (
    <Container className="mt-5">
      {/* Header */}
      <Card className="shadow-sm mb-4">
        <Card.Body className="text-center">
          <h4>Welcome, {studentName}</h4>
          <p className="text-muted">
            <Badge bg="info">{program}</Badge> | Monitoring Your Lectures
          </p>
        </Card.Body>
      </Card>

      {error && <Alert variant="danger">{error}</Alert>}

      {/* Monitoring Table */}
      <Card>
        <Card.Header className="bg-dark text-white">
          <h5><i className="bi bi-journal-text"></i> Recent Lecture Reports</h5>
        </Card.Header>
        <Card.Body>
          {loading ? (
            <p>Loading lecture data...</p>
          ) : reports.length === 0 ? (
            <Alert variant="info">No lectures reported for your class this week.</Alert>
          ) : (
            <Table striped bordered hover responsive>
              <thead className="table-light">
                <tr>
                  <th>Week</th>
                  <th>Date</th>
                  <th>Lecturer</th>
                  <th>Topic Taught</th>
                  <th>Venue</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {reports.map((r) => (
                  <tr key={r.id}>
                    <td>
                      <Badge bg="primary">Week {r.weekOfReporting}</Badge>
                    </td>
                    <td>{new Date(r.dateOfLecture).toLocaleDateString()}</td>
                    <td>{r.lecturerName}</td>
                    <td>{r.topicTaught}</td>
                    <td>{r.venue}</td>
                    <td>
                      <Button
                        size="sm"
                        variant="outline-success"
                        onClick={() => handleRate(r)}
                      >
                         Rate
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>
          )}
        </Card.Body>
      </Card>

      {/* Rating Modal */}
      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Rate This Lecture</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p><strong>Topic:</strong> {selectedReport?.topicTaught}</p>
          <p><strong>Lecturer:</strong> {selectedReport?.lecturerName}</p>
          <Form.Group>
            <Form.Label>Your Rating</Form.Label>
            <div className="d-flex gap-2 my-3">
              {[1, 2, 3, 4, 5].map((star) => (
                <Button
                  key={star}
                  variant={star <= rating ? "warning" : "secondary"}
                  onClick={() => setRating(star)}
                  style={{ width: '40px', height: '40px', fontSize: '18px' }}
                >
                  
                </Button>
              ))}
            </div>
          </Form.Group>
          <Form.Group>
            <Form.Label>Comments (Optional)</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Share your thoughts..." />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Cancel
          </Button>
          <Button variant="success" onClick={submitRating} disabled={rating === 0}>
            Submit Rating
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
}

export default StudentDashboard;