// src/pages/LoginPage.js
import React, { useState } from 'react';
import { Container, Card, Form, Button, Alert, Row, Col } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

function LoginPage({ onLogin }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const mockUsers = {
    'lecturer@luct.ac.ls': { role: 'lecturer', password: '123' },
    'prl@luct.ac.ls': { role: 'prl', password: '123' },
    'pl@luct.ac.ls': { role: 'pl', password: '123' },
    'student@luct.ac.ls': { role: 'student', password: '123' }
  };

  const handleLogin = (e) => {
    e.preventDefault();
    setError('');

    if (!email || !password) {
      setError('Please enter email and password');
      return;
    }

    const user = mockUsers[email];
    if (!user || user.password !== password) {
      setError('Invalid email or password');
      return;
    }

    // Save user role (you can use context or localStorage later)
    localStorage.setItem('userRole', user.role);

    // Redirect based on role
    switch (user.role) {
      case 'lecturer':
        navigate('/lecturer/report');
        break;
      case 'prl':
        navigate('/prl/reports');
        break;
      case 'pl':
        navigate('/pl/dashboard');
        break;
      case 'student':
        navigate('/student/dashboard');
        break;
      default:
        navigate('/');
    }

    // Optional: Call parent function if needed
    if (onLogin) onLogin(user.role);
  };

  return (
    <Container className="d-flex justify-content-center align-items-center" style={{ minHeight: '80vh' }}>
      <Card style={{ width: '400px' }}>
        <Card.Header className="bg-primary text-white">
          <h4>LUCT Reporting System</h4>
        </Card.Header>
        <Card.Body>
          <h5 className="text-center mb-4">Login</h5>
          {error && <Alert variant="danger">{error}</Alert>}

          <Form onSubmit={handleLogin}>
            <Form.Group className="mb-3">
              <Form.Label>Email</Form.Label>
              <Form.Control
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="Enter email"
                required
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder="Enter password"
                required
              />
            </Form.Group>

            <Button type="submit" variant="primary" className="w-100">
              Login
            </Button>
          </Form>

          <Row className="mt-3">
            <Col>
              <small className="text-muted">
                <strong>Test Accounts:</strong><br/>
                Lecturer: lecturer@luct.ac.ls / 123<br/>
                PRL: prl@luct.ac.ls / 123<br/>
                PL: pl@luct.ac.ls / 123<br/>
                Student: student@luct.ac.ls / 123
              </small>
            </Col>
          </Row>
        </Card.Body>
      </Card>
    </Container>
  );
}

export default LoginPage;