// src/pages/PRLReports.js
import React, { useState, useEffect } from 'react';
import {
  Container,
  Card,
  Table,
  Button,
  Modal,
  Form,
  Alert,
  Badge
} from 'react-bootstrap';

//  Import only the service functions (not 'api' directly)
import { getReports, addPRLFeedback } from '../services/api';

function PRLReports() {
  const [reports, setReports] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [selectedReport, setSelectedReport] = useState(null);
  const [feedback, setFeedback] = useState('');

  // Load all reports on component mount
  useEffect(() => {
    const fetchReports = async () => {
      try {
        const response = await getReports(); //  Uses exported service
        setReports(response.data);
        setLoading(false);
      } catch (err) {
        setError('Failed to load reports. Make sure the backend is running.');
        setLoading(false);
      }
    };

    fetchReports();
  }, []);

  // Handle opening feedback modal
  const handleOpenModal = (report) => {
    setSelectedReport(report);
    setFeedback(report.prlFeedback || '');
    setShowModal(true);
  };

  // Handle saving feedback
  const handleSaveFeedback = async () => {
    if (!feedback.trim()) {
      alert("Feedback cannot be empty.");
      return;
    }

    try {
      await addPRLFeedback(selectedReport.id, feedback); //  Correct usage

      // Update local state
      const updatedReports = reports.map(r =>
        r.id === selectedReport.id ? { ...r, prlFeedback: feedback } : r
      );
      setReports(updatedReports);
      setShowModal(false);
      alert(' Feedback saved successfully!');
    } catch (err) {
      console.error('Error saving feedback:', err);
      alert(' Failed to save feedback. Is the backend running?');
    }
  };

  return (
    <Container className="mt-5">
      <Card>
        <Card.Header className="bg-info text-white">
          <h4>
            <Badge bg="light" text="dark">PRL</Badge> Review Lecturer Reports
          </h4>
        </Card.Header>
        <Card.Body>
          {error && <Alert variant="danger">{error}</Alert>}
          
          {loading ? (
            <p>Loading reports...</p>
          ) : (
            <div>
              <h5>Total Reports: {reports.length}</h5>
              <Table striped bordered hover responsive className="mt-3">
                <thead className="table-dark">
                  <tr>
                    <th>Week</th>
                    <th>Faculty</th>
                    <th>Class</th>
                    <th>Lecturer</th>
                    <th>Course</th>
                    <th>Date</th>
                    <th>Topic Taught</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {reports.length === 0 ? (
                    <tr>
                      <td colSpan="8" className="text-center">No reports submitted yet.</td>
                    </tr>
                  ) : (
                    reports.map((r) => (
                      <tr key={r.id}>
                        <td>{r.weekOfReporting}</td>
                        <td>{r.facultyName}</td>
                        <td>{r.className}</td>
                        <td>{r.lecturerName}</td>
                        <td>
                          {r.courseCode} <br />
                          <small className="text-muted">{r.courseName}</small>
                        </td>
                        <td>{new Date(r.dateOfLecture).toLocaleDateString()}</td>
                        <td>{r.topicTaught}</td>
                        <td>
                          <Button
                            variant="outline-primary"
                            size="sm"
                            onClick={() => handleOpenModal(r)}
                          >
                            {r.prlFeedback ? 'Edit Feedback' : 'Add Feedback'}
                          </Button>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </Table>
            </div>
          )}
        </Card.Body>
      </Card>

      {/* Feedback Modal */}
      {selectedReport && (
        <Modal show={showModal} onHide={() => setShowModal(false)} size="lg">
          <Modal.Header closeButton>
            <Modal.Title>Add Feedback - Week {selectedReport.weekOfReporting}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p><strong>Topic:</strong> {selectedReport.topicTaught}</p>
            <p><strong>Lecturer:</strong> {selectedReport.lecturerName}</p>
            <p><strong>Learning Outcomes:</strong> {selectedReport.learningOutcomes}</p>

            <Form.Group className="mt-3">
              <Form.Label>Your Feedback</Form.Label>
              <Form.Control
                as="textarea"
                rows={4}
                value={feedback}
                onChange={(e) => setFeedback(e.target.value)}
                placeholder="Provide constructive feedback on lecture delivery, student engagement, content coverage..."
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={() => setShowModal(false)}>
              Cancel
            </Button>
            <Button variant="primary" onClick={handleSaveFeedback}>
              Save Feedback
            </Button>
          </Modal.Footer>
        </Modal>
      )}
    </Container>
  );
}

export default PRLReports;